#app .landscape h2:before,#app .landscape h2:after{content:attr(data-text);position:absolute;top:0;left:0;width:100%;background:#1E1E1E;clip:rect(0, 0, 0, 0)
}
#app .landscape h2:after{left:2px;text-shadow:-1px 0 red;animation:example-one-anim-1 2s 1 linear alternate-reverse
}
#app .landscape h2:before{left:-2px;text-shadow:2px 0 blue;animation:example-one-anim-2 3s 1 linear alternate-reverse
}
#app .nav{position:fixed;top:50px;width:40vw;display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around
}
#app .nav .aboutLink,#app .nav .projectLink,#app .nav .homeLink{position:relative;font-family:'Ziamimi-Bold';color:#ffffff;font-size:14px;text-decoration:none;opacity:0.8
}
#app .nav .aboutLink.router-link-active,#app .nav .projectLink.router-link-active,#app .nav .homeLink.router-link-active{opacity:1
}
#app .nav .aboutLink.router-link-active:after,#app .nav .projectLink.router-link-active:after,#app .nav .homeLink.router-link-active:after{transform:scaleX(1) !important
}
#app .nav .aboutLink:after,#app .nav .projectLink:after,#app .nav .homeLink:after{content:'';position:absolute;height:2px;width:100%;background-color:#ffffff;left:0;bottom:-5px;transition:all 0.2s ease-out;transform-origin:50% 50%;transform:scaleX(0)
}
#app .nav .aboutLink:hover:after,#app .nav .projectLink:hover:after,#app .nav .homeLink:hover:after{transform:scaleX(1)
}
#app .nav .aboutLink:hover,#app .nav .projectLink:hover,#app .nav .homeLink:hover{opacity:1
}
#app .nav .homeLink ul{width:10px;position:relative;top:-10px
}
#app .nav .homeLink ul li{width:20px;background-color:#FFFFFF;height:3px;list-style:none;position:absolute;left:calc(50% - 20px);top:0;transform:scaleX(1);transition:all 0.5s
}
#app .nav .homeLink ul li:nth-child(1){transform:rotateZ(45deg) scaleX(1)
}
#app .nav .homeLink ul li:nth-child(2){transform:rotateZ(-45deg) scaleX(1)
}
#app .nav .homeLink:after{background-color:transparent
}
#app .nav .homeLink.router-link-active{pointer-events:none
}
#app .nav .homeLink.router-link-active ul li:nth-child(1){transform:rotateZ(45deg) scaleX(0.1)
}
#app .nav .homeLink.router-link-active ul li:nth-child(2){transform:rotateZ(-45deg) scaleX(0.1)
}
#app .top{left:0%;position:absolute;width:100%;height:50vh
}
#app .bottom{right:0%;position:absolute;width:100%;top:50vh;height:50vh
}
#app .navigation-enter-active,#app .navigation-leave-active{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:1;position:relative;transition-delay:1s
}
#app .navigation-enter-active .top,#app .navigation-leave-active .top{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:0;transition-delay:1s
}
#app .navigation-enter-active .bottom,#app .navigation-leave-active .bottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);right:0;transition-delay:1s
}
#app .navigation-enter{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);position:relative;transition-delay:1s
}
#app .navigation-enter .top{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:100%;transition-delay:1s
}
#app .navigation-enter .bottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);right:100%;transition-delay:1s
}
#app .navigation-leave-to{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);position:relative;transition-delay:1s
}
#app .navigation-leave-to .top{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:-100%;transition-delay:1s
}
#app .navigation-leave-to .bottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);right:-100%;transition-delay:1s
}
#app .subnavigation-enter-active,#app .subnavigation-leave-active{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:1;position:absolute
}
#app .subnavigation-enter-active .subtop,#app .subnavigation-leave-active .subtop{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);right:0
}
#app .subnavigation-enter-active .subbottom,#app .subnavigation-leave-active .subbottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);right:0
}
#app .subnavigation-enter{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1)
}
#app .subnavigation-enter .subtop{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);right:100%
}
#app .subnavigation-enter .subbottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);right:-100%
}
#app .subnavigation-leave-to{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1)
}
#app .subnavigation-leave-to .subtop{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);right:-100%
}
#app .subnavigation-leave-to .subbottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);right:100%
}
