.mobile-enter-active{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:1;transition-delay:0.5s
}
.mobile-leave-active{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:0;transition-delay:0.5s
}
.mobile-enter{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:0;transition-delay:0.5s
}
.mobile-leave{opacity:1;transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);transition-delay:0.5s
}
@keyframes enter{
0%{opacity:0;top:-100px
}
5%{opacity:1;top:0px
}
50.9%{opacity:1;top:0px
}
55.9%{opacity:0;top:100px
}
}
@keyframes grain{
0%{transform:translate(0)
}
10%{transform:translate(-5%, -10%)
}
20%{transform:translate(-15%, 5%)
}
30%{transform:translate(7%, -25%)
}
40%{transform:translate(21%, 25%)
}
50%{transform:translate(-25%, 10%)
}
60%{transform:translate(15%, 5%)
}
70%{transform:translateY(15%)
}
90%{transform:translate(-10%, 10%)
}
}
@media screen and (max-width: 980px){
.nav{top:0 !important;width:100vw !important;padding-top:50px !important;padding-bottom:20px;background-color:#1E1E1E
}
}
@media all and (max-width: 980px) and (orientation: landscape){
#app .landscape{display:block
}
}



