
body{margin:0;background-color:#1E1E1E
}
h2{font-family:"Ziamimi-Bold";font-size:100pt;margin:auto;position:absolute;left:0;right:0
}
h2:before,h2:after{left:0;right:0;margin:auto
}
.main h2:before,.main h2:after{content:attr(data-text);position:absolute;top:0;left:0;width:100%;height:100%
}
.main h2:before{left:2px;text-shadow:-1px 0 red;background:#1E1E1E;clip:rect(44px, 450px, 56px, 0);animation:glitch-anim-1 1s infinite linear alternate-reverse
}
.main h2:after{left:-2px;text-shadow:-1px 0 blue;background:#1E1E1E;clip:rect(44px, 450px, 56px, 0);animation:glitch-anim-2 2s infinite linear alternate-reverse
}
.main .top{background-color:#1E1E1E;overflow:hidden;box-sizing:border-box;border:solid 2px  #1E1E1E
}
.main .top h2{animation:text-glitch 4s;animation-iteration-count:infinite;display:inline-block;color:#F1F1F1;bottom:-40px;margin-right:22px
}
.main .bottom{background-color:#1E1E1E;overflow:hidden;box-sizing:border-box;border:solid 2px  #1E1E1E
}
.main .bottom h2{animation:text-glitch 4s;animation-iteration-count:infinite;display:inline-block;color:#F1F1F1;top:-10px;margin-left:22px
}
.main .bottom .author{position:absolute;bottom:50px;color:#FFF;left:calc(50% - 185px);opacity:0;transition:all 1s
}
.main .bottom .author h1{font-family:'Ziamimi-Bold';margin:0
}
.main .bottom .author p{font-family:"Ziamimi-Regular";margin-top:5px;background-color:#1E1E1E;position:relative;z-index:1
}
.main .bottom .author p:after,.main .bottom .author p:before{background-color:#FFF;content:'';display:inline-block;position:absolute
}
.main .bottom .author p:after{left:0;top:5px;width:100%;height:1px;z-index:-2;transform-origin:50% 50%;transform:scaleX(0);transition:all 0.5s;transition-delay:1s
}
.main .bottom .author p:before{background-color:#1E1E1E;height:100%;width:35%;left:32.5%;z-index:-1
}
.main .bottom .author.visible{bottom:100px;opacity:1
}
.main .bottom .author.visible p:after{transform:scale(1)
}
@media screen and (max-width: 980px){
.main h2{font-size:50pt
}
.main .top h2{bottom:-20px
}
.main .bottom h2{top:-5px
}
.main .bottom .author{bottom:0 !important;left:0;right:0;width:90vw;margin:auto
}
.main .bottom .author h1{font-size:20px
}
}
@keyframes text-glitch{
0%{transform:translate(3px, 4px)
}
1%{transform:scaleY(1) skewX(0deg)
}
1.5%{transform:scaleY(3) skewX(-60deg)
}
2%{transform:scaleY(1) skewX(0deg)
}
51%{transform:scaleX(1) scaleY(1) skewX(0deg)
}
52%{transform:scaleX(1.5) scaleY(0.2) skewX(80deg)
}
53%{transform:scaleX(1) scaleY(1) skewX(0deg)
}
}
@keyframes layer-movement-before{
0%{transform:translate(10px, 5px)
}
1.5%{transform:translate(2px, 2px)
}
10%{transform:translate(0px, 0px)
}
100%{transform:translate(0px, 0px)
}
}
@keyframes layer-movement-after{
0%{transform:translate(1px, 5px)
}
0.5%{transform:translate(3px, 1px)
}
1%{transform:translate(0px, 0px)
}
70%{transform:translate(0px, 0px)
}
71%{transform:translate(2px, 2px)
}
71.5%{transform:translate(3px, 3px),skew(9deg)
}
72%{transform:translate(0px, 0px),skew(1deg)
}
100%{transform:translate(0px, 0px)
}
}
